import React, { Component } from 'react';
import { Button } from 'react-bootstrap';
import { Modal } from 'react-bootstrap';

class ComoModal extends React.Component {
    constructor () {
        super();

        this.state = {
            accionDemandaAgregada:'',
            accionConsumo:'',
            accionTasaInteres:'',
            accionInversion:'',
            accionTipoCambio:'',
            movilidadcapitales:'',
            tipocambio:'',
            esquema:'',

            show: false
          };
        this.handleInputChange = this.handleInputChange.bind(this);      
        this.onClickAceptar = this.onClickAceptar.bind(this);
      }

    onClickAceptar(){
        var resultados = this.props.todos.filter((e,i) => {
          // validar si se selecciona Demanda Agregada
          var validoDemandaAgregada = false;
          if (this.state.accionDemandaAgregada=='') validoDemandaAgregada = true;
          if (this.state.accionDemandaAgregada=='Sube'){
              if (e.Salida_EE_DemandaAgregada > 0) validoDemandaAgregada = true;
          }
          if (this.state.accionDemandaAgregada=='Baja'){
            if (e.Salida_EE_DemandaAgregada < 0) validoDemandaAgregada = true;
          }   
          // validar si se selecciona Consumo
          var validoConsumo = false;
          if (this.state.accionConsumo=='') validoConsumo = true;
          if (this.state.accionConsumo=='Sube'){
              if (e.Salida_EE_Consumo > 0) validoConsumo = true;
          }
          if (this.state.accionConsumo=='Baja'){
            if (e.Salida_EE_Consumo < 0) validoConsumo = true;
          }   
          // validar si se selecciona Tasa de interes
          var validoTasaInteres = false;
          if (this.state.accionTasaInteres=='') validoTasaInteres = true;
          if (this.state.accionTasaInteres=='Sube'){
            if (e.Salida_EE_TasaDeInteres > 0) validoTasaInteres = true;
          }
          if (this.state.accionTasaInteres=='Baja'){
            if (e.Salida_EE_TasaDeInteres < 0) validoTasaInteres = true;
          }   
          // validar si se selecciona Inversion
          var validoInversion = false;
          if (this.state.accionInversion=='') validoInversion = true;
          if (this.state.accionInversion=='Sube'){
            if (e.Salida_EE_Inversion > 0) validoInversion = true;
          }
          if (this.state.accionInversion=='Baja'){
            if (e.Salida_EE_Inversion < 0) validoInversion = true;
          }   
          // validar si se selecciona TipoCambio
          var validoTipoCambio = false;
          if (this.state.accionTipoCambio=='') validoTipoCambio = true;
          if (this.state.accionTipoCambio=='Sube'){
            if (e.Salida_EE_TipoDeCambio > 0) validoTipoCambio = true;
          }
          if (this.state.accionInversion=='Baja'){
            if (e.Salida_EE_TipoDeCambio < 0) validoTipoCambio = true;
          }   

          return (validoDemandaAgregada && validoConsumo && validoTasaInteres && validoInversion && validoTipoCambio);
        })
        // for (var i = 0; i < this.props.todos.length; i++) {
        //   var valido = false;
        //   if (this.state.accionDemandaAgregada == 'Sube'){
        //       if (this.props.todos[i].Salida_EE_DemandaAgregada > 0){
        //             valido = true;
        //       }
        //   }
        //   if (valido){
        //     this.props.onAddSeleccionado(this.props.todos[i])
        //     console.log('Encontro ' + i + ' '  + this.props.todos[i].Entrada_Accion );
        //   }
        // }

        //devolver resultados a la funcion que se pasó como parametro
        this.props.onAddSeleccionado(resultados)

        //Cerrar form modal
        this.props.onHide();
    }

    handleInputChange(e) {
        const {value, name} = e.target;
        console.log(value, name);
        this.setState({
          [name]: value
        });
    }
    
  render() {
    return (
      <Modal
        {...this.props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
            Como ...
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <p>
                Detalla las acciones o situaciones que lleva a producir la accion seleccionada.
                Con la configuracion de parametros indicados.
            </p>
            <div class="row">
                <div class="col-sm-4" >
                Demanda Agregada
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionDemandaAgregada"
                    className="form-control"
                    value={this.state.accionDemandaAgregada}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
                <div class="col-sm-4" >
                Consumo
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionConsumo"
                    className="form-control"
                    value={this.state.accionConsumo}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4" >
                Tasa de interes
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionTasaInteres"
                    className="form-control"
                    value={this.state.accionTasaInteres}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
                <div class="col-sm-4" >
                Inversion
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionInversion"
                    className="form-control"
                    value={this.state.accionInversion}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4" >
                Tipo de cambio
                </div>
                <div class="col-sm-2" >
                <select
                    name="accionTipoCambio"
                    className="form-control"
                    value={this.state.accionTipoCambio}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Sube</option>
                    <option>Baja</option>
                </select>
                </div>
            </div>
            <div class="row">
                <div class="col-sm-4">
                Movilidad de Capitales
                <select
                    name="movilidadcapitales"
                    className="form-control"
                    value={this.state.movilidadcapitales}
                    onChange={this.handleInputChange}                    
                    >
                    <option></option>
                    <option>Nula</option>
                    <option>Baja</option>
                    <option>Alta</option>
                    <option>Perfecta</option>
                </select>
                </div>
                <div class="col-sm-4">
                Tipo Cambio
                <select
                    name="tipocambio"
                    className="form-control"
                    value={this.state.tipocambio}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Flexible</option>
                    <option>Fijo</option>
                </select>
                </div>
                <div class="col-sm-4">
                Esquema
                <select
                    name="esquema"
                    className="form-control"
                    value={this.state.esquema}
                    onChange={this.handleInputChange}
                    >
                    <option></option>
                    <option>Intermedio</option>
                    <option>Keynesiano</option>
                    <option>Clasico</option>
                </select>
                </div>
            </div>

        </Modal.Body>
        <Modal.Footer>
          <Button onClick={this.onClickAceptar}>Aceptar</Button>            
          <Button onClick={this.props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default ComoModal;